<template>
  <v-container>
      <v-toolbar
        color="cyan"
        dark
      >
        <v-toolbar-title>SET UP LAND CLASSIFICATION</v-toolbar-title>
      </v-toolbar>
    <v-card class="mx-auto card-padding">

        <template>
          <v-dialog v-model="dialogAddUpdate" persistent max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-row>
                <v-col>
                  <v-row>
                    <v-col>
                      <v-text-field
                          label="Search Bar"
                          v-model="Search"
                          @keyup="getSearch"

                        ></v-text-field>
                    </v-col>
                    <v-col>
                          <v-combobox
                            v-model="limitRow"
                            label="Show"
                            :items="allLimit"
                            @change="limitFunction"
                        
                          >
                      </v-combobox>
                    </v-col>
                  </v-row>
                  <v-btn color="primary" dark v-bind="attrs" v-on="on">
                    Add New
                  </v-btn>
                </v-col>
                <v-col>
                  <!-- <v-btn @click="getSearch">Search</v-btn> -->
                   <v-btn
                    class="float-right mini-margin-left"  @click="nextFunction"
                    >Next</v-btn
                  >
                  <v-btn class="float-right" @click="prevFunction">Prev</v-btn>
                  <input v-model="lgetCurrentrow" type="hidden" />
                </v-col>
              </v-row>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">Add Land Classification</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                        <v-text-field
                            label="Land Classification"
                            v-model="txtLandClass"
                        ></v-text-field>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogAddUpdate = false"
                  >Close</v-btn
                >
                <v-btn color="blue darken-1" text @click="insertLand">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>

        <!-- Modal Delete Res -->
        <template>
          <v-dialog v-model="dialogDelete" persistent max-width="600px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <h2>Are sure want to delete ?</h2>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogDelete = false"
                  >No</v-btn
                >
                <v-btn color="info darken-1" text @click="deleteLand">Yes</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>

        <template>
          <v-simple-table class="mini-margin-top">
            <template v-slot:default>
              <thead>
                <tr>
                  <th>Actions</th>
                  <th>#</th>
                  <th>Land Classification</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="res in allLand" :key="res.id">
                  <td>
                    <v-btn color="info" depressed x-small @click="getEdit(res)" fab>
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                    |
                    <v-btn
                      color="error"
                      depressed
                      fab
                      x-small
                      @click="getDelete(res)"
                    >
                      <v-icon>mdi-delete-alert-outline</v-icon>
                    </v-btn>
                  </td>
                  <td>{{res.id}}</td>
                  <td>{{res.class}}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </template>

    </v-card>
  </v-container>
</template>

<script>

import { mapGetters, mapActions } from "vuex";

export default {
  name: "LandClass",

  data: () => ({

    allLimit:[5,10,20,50,100],
    limitRow:[5],
    dialogAddUpdate:false,
    Search:"",
    dialogDelete: false,
    id:"",
    txtLandClass:""

  }), methods: {
    ...mapActions([
      "fetchLandList",
      "increment",
      "decrement",
      "searchLandList",
      "deletePlantList",
      "CreateLandClass",
      "UpdateLandClass",
      "deleteLandList"
     
    ]),
    nextFunction() {
      this.increment({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
       this.fetchLandList({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
       this.fetchLandList({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
    },
    limitFunction(){
      this.fetchLandList({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
    },
    getSearch(){
      this.searchLandList({
        txtsearch: this.Search,
        limit: this.limitRow
      });
    },
    getEdit(res){
        this.dialogAddUpdate = true;
        this.id = res.id;
        this.txtLandClass = res.class;
    },
    getDelete(res) {
      this.dialogDelete = true;
      this.id = res.id;
    },
    deleteLand(){
      this.deleteLandList({
          id: this.id,
          offset: this.lgetCurrentrow,
          limit: this.limitRow
      });
      this.dialogDelete = false;
    },
   
    insertLand(){
      var id = this.id;

      if(id==""){
        this.CreateLandClass({
            class :this.txtLandClass,
            offset: this.lgetCurrentrow,
            limit: this.limitRow
        });
      }else{
          this.UpdateLandClass({
            id: this.id,
            class :this.txtLandClass
        });
        this.dialogAddUpdate = false;
      }
      
      this.txtLandClass = "";
      this.id = "";
    }
  },
  computed: mapGetters(["allLand", "lgetCurrentrow"]),
  created() {
    this.fetchLandList({
        offset: this.lgetCurrentrow,
        limit: this.limitRow
      });
  },
  
};
</script>