<template>
    <v-container>
      <v-toolbar
        color="cyan"
        dark
        >
        <v-toolbar-title>SET UP USER RESPONSIBILITY</v-toolbar-title>
      </v-toolbar>
      <v-card class="mx-auto card-padding">

        <!-- Modal Insert Res -->
        <template>
          <v-dialog v-model="dialogAdd" persistent max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-row>
                <v-col>
                  <v-row>
                    <v-col>
                      <v-text-field
                          label="Search Bar"
                          v-model="userSearch"
                          @keyup="getSearch"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                          <v-combobox
                            v-model="limitRow"
                            label="Show"
                            :items="allLimit"
                            @change="limitFunction"
                          >
                      </v-combobox>
                    </v-col>
                  </v-row>
                  <v-btn color="primary" dark v-bind="attrs" v-on="on">
                    Add New
                  </v-btn>
                </v-col>
                <v-col>
                  <!-- <v-btn @click="getSearch">Search</v-btn> -->
                  <v-btn
                    class="float-right mini-margin-left"  @click="nextFunction"
                    >Next</v-btn
                  >
                  <v-btn class="float-right" @click="prevFunction">Prev</v-btn>
                  <input v-model="rgetCurrenrow" type="hidden" />
                </v-col>
              </v-row>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">Add New Responsibility</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        label="Access Level Name"
                        v-model="access_level"
                        required
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-combobox
                        v-model="modules"
                        label="Modules"
                        :items="allModule"
                        outlined
                        dense
                        multiple
                      >
                      </v-combobox>
                    </v-col>
                  </v-row>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogAdd = false"
                  >Close</v-btn
                >
                <v-btn color="blue darken-1" text @click="insertRes">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>

        <!-- Modal Update Res -->
        <template>
          <v-dialog v-model="dialogEdit" persistent max-width="600px">
            <v-card>
              <v-card-title>
                <span class="headline">Update User Details</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        label="Access Level Name"
                        v-model="access_level_edit"
                        required
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-select
                          v-model="modules_edit"
                          label="Modules"
                          :items="allModule"
                          outlined
                          dense
                          multiple
                        >
                        <option v-for="option in allModule" v-bind:key="option.value">
                          {{ option.text }}
                        </option>
                      </v-select>
                    </v-col>
                  </v-row>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogEdit = false"
                  >Close</v-btn
                >
                <v-btn color="blue darken-1" text @click="updateRes"
                  >Update</v-btn
                >
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>

        <!-- Modal Delete Res 
        <template>
          <v-dialog v-model="dialogDelete" persistent max-width="600px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <h2>Are sure want to delete ?</h2>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogDelete = false"
                  >No</v-btn
                >
                <v-btn color="info darken-1" text @click="deleteRes">Yes</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
          -->
        <!-- Modal Table Res -->
        <template>
          <v-simple-table class="mini-margin-top">
            <template v-slot:default>
              <thead>
                <tr>
                  <th>Actions</th>
                  <th>#</th>
                  <th>Responsibility</th>
                  <th>Modules</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="res in allRes" :key="res.id">
                  <td>
                     <v-btn color="info" depressed x-small @click="getEdit(res)" fab>
                     <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  <!-- <v-btn
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(res)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn> -->
                  </td>
                  <td>{{ res.id }}</td>
                  <td>{{ res.access_level }}</td>
                  <td>{{ res.modules }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </template>

      </v-card>
    </v-container>
</template>

<script>

import { mapGetters, mapActions } from "vuex";

export default {
  name: "Responsibility",
  data() {
    return {
       dialogEdit: false,
       dialogAdd: false,
       dialogDelete: false,
       userSearch: "",
       access_level:"",
       userSearch:"",
       allUsers:[],
       modules: [],
       access_level_edit:"",
       modules_edit:"",
       id:"",
       allLimit:[5,10,20,50,100],
       limitRow:[5]
    };
  },
  methods: {
    ...mapActions([
      "fetchRes",
      "fetchModules",
      "CreateRes",
      "updateRess",
      // "deleteRess",
      "searchRes",
      "increment",
      "decrement",
    ]),
   
    nextFunction() {
      this.increment({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
       this.fetchRes({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
       this.fetchRes({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
    },
    insertRes() {
      this.CreateRes({
        access_level: this.access_level,
        modules: this.modules,
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
      this.access_level = "";
      this.modules = "";
      // this.fetchRes({
      //   offset: this.rgetCurrenrow,
      //   limit: this.limitRow
      // });
    },
    getEdit(res) {
      this.dialogEdit = true;
      this.id = res.id;
      this.access_level_edit = res.access_level;
      this.modules_edit = res.modules.split(",");
     
    },
    updateRes(){
      this.updateRess({
        id: this.id,
        access_level: this.access_level_edit,
        modules: this.modules_edit
      });
    },
    // getDelete(res) {
    //   this.dialogDelete = true;
    //   this.id = res.id;
    // },
    // deleteRes() {
    //   this.deleteRess({
    //     offset: this.rgetCurrenrow,
    //     limit: this.limitRow,
    //     id: this.id,
    //   });
    //   this.dialogDelete = false;
    // },
    getSearch() {
      this.searchRes({
        txtsearch: this.userSearch,
        limit: this.limitRow
      });
    },
    limitFunction(){
      this.fetchRes({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
    }
  },
  computed: mapGetters(["allRes", "rgetCurrenrow","allModule"]),
  created() {
    this.fetchRes({
        offset: this.rgetCurrenrow,
        limit: this.limitRow
      });
    this.fetchModules();
  },
};
</script>