  created() {
    this.fetchLandList({
      offset: 0,
      limit: 100,
    }).then(() => {
      this.pushLandClass(this.allLand);
    });

    this.fetchNatureList({
      offset: 0,
      limit: 100,
    }).then(() => {
      this.pushNature(this.allNature);
    });
  },
  components: {
    Planning,
  },
  methods: {
    ...mapActions(["fetchLandList", "fetchNatureList"]),
    pushLandClass(land) {
      let new_item = JSON.parse(JSON.stringify(land));
      // console.log(land);
      // console.log(new_item);
      let landLength = new_item.length;
      for (let i = 0; i < landLength; i++) {
        this.editFormObject[3].editableForm[0].items.push(new_item[i].class);
        this.editFormObject[4].editableForm[0].items.push(new_item[i].class);
      }
    },
    pushNature(business) {
      let new_item = JSON.parse(JSON.stringify(business));
      let landLength = new_item.length;
      for (let i = 0; i < landLength; i++) {
        this.editFormObject[2].editableForm[0].items.push(
          new_item[i].nature_of_business
        );
      }
    },
  },
  computed: {
    ...mapGetters(["allLand", "allNature"]),
  },
};
</script>