<template>
  <v-container>
    <v-toolbar
        color="cyan"
        dark
      >
        <v-toolbar-title>USER MANAGEMENT</v-toolbar-title>
    </v-toolbar>
    <v-card class="mx-auto card-padding">
      <template>
        <v-dialog v-model="dialogAdd" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col>
                 <v-row>
                    <v-col>
                      <v-text-field
                          label="Search Bar"
                          v-model="userSearch"
                          @keyup="getSearch"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                          <v-combobox
                            v-model="limitRow"
                            label="Show"
                            :items="allLimit"
                            @change="limitFunction"
                          >
                      </v-combobox>
                    </v-col>
                  </v-row>
                <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn>
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn class="float-right" @click="prevFunction">Prev</v-btn>
                <input v-model="getCurrenrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Add New User</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>   
                  <v-col cols="6">
                      <v-text-field
                          label="Search by First or Last Name"
                          @change="getEmployee"
                          v-model="iisemployee"
                          required
                        >
                        </v-text-field>     
                  </v-col>
                  <v-col cols="6">
                     <v-btn icon style="margin:20px 0px 0px 0px !important;">
                        <v-icon>mdi-magnify</v-icon>
                      </v-btn>
                  </v-col>
                  <v-col cols="12">
                    <v-simple-table class="mini-margin-top">
                      <template v-slot:default>
                        <thead>
                          <tr>
                            <th>Selection</th>
                            <th>IIS Employee Number</th>
                            <th>Name</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="iis in allIisEmployee" :key="iis.id">
                            <td>
                              <v-btn color="info" depressed x-small @click="selectIISEmp(iis)" fab>
                                <v-icon>mdi-selection-multiple</v-icon>
                              </v-btn>
                            </td>
                            <td>{{ iis.id }}</td>
                            <td>{{ iis.name }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      label="IIS Employee Number *"
                      v-model="iis_employee_number"
                      required
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field label="Full Name *" v-model="name" required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-combobox
                      :items="allAccess"
                      v-model="user_access_level"
                      label="Responsibility"
                      outlined
                      dense
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogAdd = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="insertUser">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Update Product -->
      <template>
        <v-dialog v-model="dialogEdit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">Update User Details</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      label="IIS Employee Number *"
                      v-model="iis_employee_number_edit"
                      required
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      label="Full Name *"
                      v-model="name_edit"
                      required
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                      <v-select
                        :items="allAccess"
                        v-model="user_access_level_edit"
                        label="Responsibility"
                        outlined
                        dense
                      >
                      <option v-for="option in allAccess" v-bind:key="option.value">
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                </v-row>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogEdit = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="updateUser"
                >Update</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Delete Product -->
      <template>
        <v-dialog v-model="dialogDelete" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are sure want to delete ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogDelete = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="deleteUser">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <template>
        <v-simple-table class="mini-margin-top">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th>#</th>
                <th>Name</th>
                <th>IIS Number</th>
                <th>Responsibility</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="user in allUsers" :key="user.id">
                <td>
                  <v-btn color="info" depressed x-small @click="getEdit(user)" fab>
                     <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  |
                  <v-btn
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(user)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn>
                </td>
                <td>{{ user.id }}</td>
                <td>{{ user.name }}</td>
                <td>{{ user.iis_employee_number }}</td>
                <td>{{ user.user_access_level }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
      
    </v-card>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "UserManagement",
  data() {
    return {
      dialogAdd: false,
      dialogEdit: false,
      dialogDelete: false,
      user_access_level: "",
      iis_employee_number: "",
      name: "",
      userSearch: "",
      userID: "",
      user_access_level_edit: "",
      iis_employee_number_edit: "",
      name_edit: "",
      allLimit:[5,10,20,50,100],
      limitRow:[5],
      iisemployee:""
    };
  },
  methods: {
    ...mapActions([
      "fetchUsers",
      "createUser",
      "increment",
      "decrement",
      "searchUsers",
      "updateUsers",
      "deleteUsers",
      "fetchAccess",
      "getIssEmployee"
    ]),
    selectIISEmp(iis){
      this.iis_employee_number = iis.id;
      this.name = iis.name
    },
    insertUser() {
      this.createUser({
        id: null,
        iis_employee_number: this.iis_employee_number,
        name: this.name,
        user_access_level: this.user_access_level,
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
      this.iis_employee_number = "";
      this.name = "";
      this.user_access_level = "";
      //  this.fetchUsers({
      //   offset: this.getCurrenrow,
      //   limit: this.limitRow
      // });
    },
    nextFunction() {
      this.increment({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
       this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
       this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
    },
    getSearch() {
      this.searchUsers({
        txtsearch: this.userSearch,
        limit: this.limitRow
      });
    },
    getEdit(user) {
      this.dialogEdit = true;
      this.iis_employee_number_edit = user.iis_employee_number;
      this.userID = user.id;
      this.name_edit = user.name;
      this.user_access_level_edit = user.user_access_level;
    },
    updateUser() {
      this.updateUsers({
        id: this.userID,
        iis_employee_number: this.iis_employee_number_edit,
        name: this.name_edit,
        user_access_level: this.user_access_level_edit,
      });
    },
    getDelete(user) {
      this.userID = user.id;
      this.dialogDelete = true;
    },
    deleteUser() {
      this.deleteUsers({
          id: this.userID,
          offset: this.getCurrenrow,
          limit: this.limitRow
      });
      this.dialogDelete = false;
    },
    limitFunction(){
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
    },
    getEmployee(){
      this.getIssEmployee(this.iisemployee);
    }
  },
  computed: mapGetters(["allUsers", "getCurrenrow","allAccess","allIisEmployee"]),
  created() {
    this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow
      });
    this.fetchAccess();
  },
};
</script>